import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

class Contact extends Component {
    render() {
        return(
           <div className="contact-body">
            <Grid className="contact-grid">
                <Cell col={6}>
                    <h2>Hemant Prasad</h2>
                    <img src="data:image/jpeg;base64," alt="avatar"
                    style={{height: '250px'}}/>
                    <p style={{width: '75%', margin: 'auto', paddingTop: '1em' }}>Hello there! I'd like to work in a challenging environment where my skills and knowledge are utilized for the achievement of the objectives of the organization.</p>
                </Cell>

                <Cell col={6}>
                    <h2>Contact Me</h2>
                    <hr/>
                    
                    <div className="contact-list">
                    <List>

                    <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Comfortaa'}}>
                            <i className="fa fa-phone-square" aria-hidden="true"/>
                            +91-7278656823
                        </ListItemContent>
                    </ListItem>

                    <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Comfortaa'}}>
                            <i className="fa fa-envelope" aria-hidden="true"/>
                            imhkp4u@gmail.com
                        </ListItemContent>
                    </ListItem>

                    <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Comfortaa'}}>
                            <i className="fa fa-skype" aria-hidden="true"/>
                            imhkp4u
                        </ListItemContent>
                    </ListItem>

                    <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Comfortaa'}}>
                            <i className="fa fa-fax" aria-hidden="true"/>
                            033-65465654
                        </ListItemContent>
                    </ListItem>

                    </List>
                    </div>

                </Cell>
            </Grid>
           </div>
        )
    }
}

export default Contact;